body {
	font-family: $default-font;
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	margin-top: 200px;
}
button {
	border: none;
	cursor: pointer;
	outline: none;
	border-radius: 4px;
	background: #1a3e3e;
	padding: 15px 36px;
	color: #fdfdfd;
	margin-left: 5px;
}
input {
	width: 280px;
	height: 44px;
	padding: 0px 16px;
	align-items: center;
	border-radius: 4px;
	background: #f3f5f4;
	border: 1px solid #afc7cf;
	outline: none;
}
a:hover {
	color: #27815d;
	text-decoration-line: underline;
}
.container {
	max-width: 1280px;
	// padding: 0 20px;
	margin: 0 auto;
	text-align: center;
}
